import {Link} from '../routes'

import { Typography, Toolbar, Button, AppBar, Grid } from '@material-ui/core'
import Computer from '@material-ui/icons/Computer'
import Pets from '@material-ui/icons/Pets'
import Work from '@material-ui/icons/Work'
import blueGrey from '@material-ui/core/colors/blueGrey'

import PropTypes from 'prop-types'

class Footer extends React.Component {

    render() {
  
      const { classes } = this.props;
  
      return (
        <div style={{backgroundColor: blueGrey[100]}}>
            <div className={classes.innerContainer}>
                <div className={classes.contentSection} style={{margin: 'auto'}}>
                        <div item xs={4} className={classes.footerIcon}>
                            <Work />
                            <Typography variant="body2">
                                LinkedIn
                            </Typography>
                        </div>
                        <div item xs={4} className={classes.footerIcon}>
                            <Computer />
                            <Typography variant="body2">
                                Github
                            </Typography>
                        </div>
                        <div item xs={4} className={classes.footerIcon}>
                            <Pets />
                            <Typography variant="body2">
                                #BrindleCon
                            </Typography>
                        </div>
                </div>
            </div>
        </div>
      )
    }
}

Footer.propTypes = {
    classes: PropTypes.object.isRequired
}

export default Footer


